.promo-modal-container {
  position: fixed;
  inset: 0;
  z-index: 15;
  background-color: rgba(0, 0, 0, 0.5);
  margin: auto;
}
.promo-modal-container .promo-input-container {
  /* position: absolute; */
  /* inset: 0; */
 width: 100%;
  max-height: 100%; /* Maximum height to allow scrolling */
  overflow-y: auto; 
}
.promo-modal-container .promo-content{
  background-color: rgb(255, 255, 255);
  margin: auto;
  width: 40%;
  /* height: 520px; */
  /* text-align: center; */
  /* border-radius: 10px; */
  /* padding: 2em 2em 3em; */
  position: relative;
  margin-top: 2em;
  /* display: flex; */
}
 
.promo-modal-container .cross-tab-icon {
  position: absolute;
  top: 8px;
  right: 20px;
  cursor: pointer;
}
.promo-img{
  width: 50%;
  height: 50vh;
}
.promo-img img{
  width: 100%;
  object-fit: cover;
  height: 100%;
}

.promo-details{
 padding: 2em;
}
.promo-input-container h2 {
  color: rgb(33, 33, 33);
  margin:  10px 0;
  font-weight: 500;
  /* text-align: center; */
  font-size: 2rem;
  font-weight: 600;
}


.promo-modal-container
.name-container {
  display: flex;
  flex-flow: column nowrap;
  padding: .5em 1.5em;
  
}

.promo-modal-container .name-container > label {
  margin-bottom: 5px;
  font-size: .9em;
  color: rgb(108, 108, 108);;
}

.promo-modal-container .name-container input {
  padding: 10px 10px;
  border-radius: 5px;
  border: 1px solid rgb(210, 210, 210);
}
