  padding: 8px 16px;
  display: block;
  width: 100%;
  border: 1px solid rgb(226, 226, 226);
  border-radius: 5px;
} */

.promo-modal-container .submit {
  padding: 15px 10px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 1.1rem;
  border: none;
  margin-bottom: 10px;
  color: white;

  background-color: var(--primary-color);
}

.promo-modal-container .submit:hover {
  cursor: pointer;
  background-color: black;
}
.promo-modal-container .btn-container {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
}

.promo-modal-container .btn-container button {
  border: none;
  padding: 8px 16px;

  border-radius: 5px;
}

.promo-modal-container .btn-container button:hover {
  cursor: pointer;
  background-color: var(--secondary-background-color-onhover);
}

.promo-modal-container .btn-container button:last-child {
  flex-grow: 1;
  margin-left: 10px;
}
 
@media (max-width: 767px) {

  .promo-modal-container .promo-content{
    width: 90%;
  }
}
