/* ProductImage.css */

.product-details-image {
  /* justify-content: center; */
  /* align-items: center; */
  /* height: 100%; */
}

.product-details-image img {
 width: 100%;
 height: 100%; 
  object-fit: contain;
}
.slick-slide {
  padding: 0 .1em;
}
.slick-slider button{
  display: none !important;
}
@media (max-width: 767px) {
  .product-details-image{
    /* height: 40vh; */
    width: 100%;
  }
  .product-details-image img{
    /* height: 40vh; */
    width: 100%;
    object-position: top;
    object-fit: cover;
  }
}
@media (min-width: 768px) {
  .product-details-image  {
    width:  65%;
    padding: 0em 4em 0;
  }
  .product-details-image img {
    width: 100%;
    /* max-height: 500px; */
  }
}
